.form {
  width: 320px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 30px;
}

.label {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
  font-weight: bold;
}
input {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 300px;
  margin-top: 5px;
}
button {
  padding: 10px 20px;
  background-color: #e9edf1;
  color: #080707;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
button:hover {
  background-color: yellow;
}
